'use client';
import * as React from 'react';
import Box from '@mui/material/Box';
import Drawer from '@mui/material/Drawer';
import Button from '@mui/material/Button';
import FileSystemTree from './FileSystemTree';
import FolderIcon from '@mui/icons-material/Folder';

export default function FileSystemDrawer() {
  const [open, setOpen] = React.useState(false);

  const toggleDrawer = (newOpen: boolean) => () => {
    setOpen(newOpen);
  };

  const DrawerList = (
    <Box
      sx={{
        width: 250,
        height: '100%', // Ensure the drawer spans the full height
        display: 'flex',
        flexDirection: 'column',
      }}
      role="presentation"
      onClick={toggleDrawer(false)} // Close the drawer when clicking outside the tree
      onKeyDown={toggleDrawer(false)}
    >
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          p: 2,
          borderBottom: '1px solid #ddd', // Add a divider for better visual separation
        }}
      >
        <Button
          onClick={toggleDrawer(false)}
          sx={{ minWidth: 'unset', p: 0 }}
        >
          <FolderIcon fontSize="large" />
        </Button>
      </Box>
      <Box sx={{ flex: 1, overflowY: 'auto', mt: 2, px: 1 }}>
        <FileSystemTree />
      </Box>
    </Box>
  );

  return (
    <div>
      <Button onClick={toggleDrawer(true)}>
        <FolderIcon fontSize="large" />
      </Button>
      <Drawer open={open} onClose={toggleDrawer(false)}>
        {DrawerList}
      </Drawer>
    </div>
  );
}
