function renderTree(
  node: TreeViewBaseItem,
  handleNodeClick: (nodeId: string, currNode: HTMLElement) => void,
  handleRightClick: (
    event: React.MouseEvent,
    type: 'file' | 'folder' | 'empty',
    node?: TreeViewBaseItem
  ) => void,
  handleCloseContextMenu: () => void
): React.ReactNode {
  return (
    <TreeItem
      key={node.id}
      itemId={node.id}
      data-itemid={node.id}
      label={node.label}
      onClick={async (event) => {
        await handleNodeClick(node.id, event.currentTarget as HTMLElement);
      }}
      onContextMenu={(event) =>
        handleRightClick(event, node.hasChildren ? 'folder' : 'file', node)
      }
      sx={{
        '.MuiTreeItem-content': {
          display: 'flex',
          alignItems: 'center', // Ensures label aligns with icon
          paddingLeft: '8px', // Adjust spacing as needed
        },
        '.MuiTreeItem-label': {
          marginLeft: '8px', // Spacing between icon and label
        },
        '.MuiTreeItem-iconContainer': {
          marginRight: '4px', // Adjust spacing around the icon
        },
      }}
    >
      {node.children?.map((child) =>
        renderTree(child, handleNodeClick, handleRightClick, handleCloseContextMenu)
      )}
    </TreeItem>
  );
}
